import { Cloud } from 'lucide-react'
import React from 'react'

export default function Header() {
  return (
    <header className='mx-30 my-10 py-3 px-5 border rounded-full border-slate-300 flex justify-between items-center'>
        <div>
            {/* Logo */}
            <div>
                <Cloud className='text-primary font-bold' size={40}/>
            </div>
        </div>
    </header>
  )
}
